
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import ImageUploader from "@/components/common/ImageUploader";
import Address from "@/components/common/Address";
import { SeourceCreatedSubmit } from "@/servers/api";

export default {
props: ["showAddress"],
data() {
return {
inputValue: "",
textareaValue: "",
imgList: [],
arressVal: ""
};
},
components: {
XTextarea,
Group,
XInput,
XButton,
ImageUploader,
Address
},
methods: {
getImgAddress(val) {

this.arressVal = val;
},
getImgUrl(val) {

this.imgList = val;
},
showToast(content) {
this.$vux.toast.text(content, "mid");
setTimeout(() => {
this.$vux.toast.hide();
}, 2000);
},
formData() {
if (!this.inputValue) {
this.showToast("请输入标题");
return;
} else if (!this.textareaValue) {
this.showToast("请输入内容");
return;
} else if (!this.imgList.length) {
this.showToast("请上传图片");
return;
} else if (!this.arressVal) {
this.$vux.confirm.show({
title: "提示",
content: "发布地点未选，确定要发布吗？",
onCancel() {
return;
},
onConfirm: () => {
this.submitForm();
}
});
} else {
this.submitForm();
}
},
onEvent(event) {

},
getInputVal() {

},
getTextareaValue() {

},
submitForm() {
SeourceCreatedSubmit({
type: this.$route.query.branch,
title: this.inputValue,
summary: this.textareaValue,
image: this.imgList.join(","),
address: this.arressVal
})
.then(res => {
console.log(res);
if (res.result === 1) {
this.$vux.toast.show({
type: "success",
text: "发布成功",
time: 1000,
onHide: () => {
this.$router.goBack();
}
});
} else {
this.$vux.toast.show({
type: "warn",
text: "失败，请重试",
time: 1000
});
}
})
.catch(err => {
console.log(err);
});
}
},
beforeDestroy() {
this.inputValue = "";
this.textareaValue = "";
this.imgList = [];
this.arressVal = "";
}
};



